import { Controller } from '@ali/4ever-cangjie';
import { mergeData } from '../../utils';

export default function updateFindText(
  controller: Controller,
  findText: string,
) {
  const { data } = controller.value;
  const prevFindText = data.findText;

  return mergeData(controller, {
    findText,
    prevFindText,
  });
}
